import React, { ReactNode, useEffect, useState } from "react";
import { Tooltip } from "antd";
import {
  CloseOutlined,
  CopyOutlined,
  LeftSquareOutlined,
  RightSquareOutlined,
  PlusSquareOutlined,
} from "@ant-design/icons";
import GridLayout, { Layout } from "react-grid-layout";

import { CONSTANTS, BLOCK_STYLE, TYPES_FN, NO_Edit_TYPE } from "./constants";
import Header from "./components/header";
import Setting from "./components/setting";

import { getUrlBase64 } from "../../utils";

export type Data = {
  text?: ReactNode;
  type?: string;
  title?: string;
  theme?: string;
  layout?: Partial<Layout>;
  style?: React.CSSProperties;
};

export type BgNodes = React.CSSProperties;
export interface EnhanceLayout extends Layout {
  data: Data;
}

function App() {
  const [page, setPage] = useState(0);
  const [total, setTotal] = useState(1);
  const [layouts, setLayouts] = useState<Layout[][]>([[]]);
  const [blockData, setBlockData] = useState<Record<string, Data>[]>([{}]);
  const [theme, setTheme] = useState("green");
  const [bgNodes, setBgNodes] = useState<BgNodes[]>([]);
  const [printNodes, setPrintNodes] = useState<string[]>([]);

  useEffect(() => {
    Object.keys(blockData[page]).forEach(async (item) => {
      const type = item.split("-")[0];
      switch (type) {
        // urlbase64
        case "avatar": {
          if (typeof blockData[page][item].text === "string") {
            const v = await getUrlBase64(
              blockData[page][item].text as string,
              "png"
            );
            setBlockData((data) => {
              const arr = [...data];
              arr[page][item] = {
                ...arr[page][item],
                text: <img alt="" className="h-full" src={v} />,
              };
              return arr;
            });
          }
          break;
        }
        // 
        case "divider": {
          const { type, title, theme: bTheme } = blockData[page][item];

          if (typeof bTheme === "string" && bTheme !== theme) {
            setBlockData((data) => {
              const arr = [...data];
              arr[page][item] = {
                ...blockData[page][item],
                text: TYPES_FN(type as string, title, theme),
                theme,
              };
              return arr;
            });
          }
          break;
        }
        case "icon": {
          const { text } = blockData[page][item];
          if (typeof text === "string") {
            setBlockData((data) => {
              const arr = [...data];
              arr[page][item] = {
                ...blockData[page][item],
                text: <i className={`iconfont ${text}`} />,
              };
              return arr;
            });
          }
          break;
        }
      }
    });
  }, [blockData, theme, page]);

  const addBlock = ({
    type = "p",
    text = "",
    title,
    theme,
    layout = {},
    style = {},
  }: Data) => {
    console.log(layouts, "layouts");

    const key = layout.i || "p-" + Date.now() + layouts[page].length;
    // 
    if (!layout.y && layout.y !== 0) {
      const lastY = layouts[page].reduce((pre, cur) => Math.max(pre, cur.y), 0);
      layout.y = lastY;
      layout.x = 0;
    }

    layout.i = key;

    setLayouts((layouts) => {
      const arr = [...layouts];
      arr[page].push(layout as Layout);
      console.log(arr);

      return arr;
    });

    setBlockData((data) => {
      const arr = [...data];
      arr[page][key] = {
        type,
        text,
        title,
        theme,
        style,
      };
      return arr;
    });
  };

  const handleLayoutChange = (layouts: Layout[], i: number) => {
    console.log(layouts, "layouts");

    setLayouts((pre) => {
      const arr = [...pre];
      arr[i] = layouts;
      return arr;
    });
  };

  const handleBlockChange = (
    key: string,
    e: React.FocusEvent<HTMLParagraphElement>
  ) => {
    const node = e.target as HTMLElement;
    setBlockData((pre) => {
      const data = [...pre];
      data[page][key].text = node.innerText;
      return data;
    });
  };

  const handleCopyBlock = (key: string) => {
    const i = layouts.findIndex((item) => item[page].i === key);

    i !== -1 &&
      addBlock({
        type: blockData[page][key].type,
        text: blockData[page][key].text,
        layout: {
          ...layouts[i],
          i: layouts[i][page].i.split("-")[0] + "-" + Date.now(),
        },
        style: blockData[page][key].style,
      });
  };

  const handleDeleteBlock = (key: string) => {
    const i = layouts.findIndex((item) => item[page].i === key);
    setLayouts((pre) => {
      const arr = [...pre];
      arr[page].splice(i, 1);
      return arr;
    });
    if (blockData[page][key]) {
      setBlockData((pre) => {
        const data = [...pre];
        delete data[page][key];
        return data;
      });
    }
  };

  const handleDeleteBgNode = (i: number) => {
    setBgNodes((n) => {
      const arr = [...n];
      arr.splice(i, 1);
      return arr;
    });
  };
  // console.log(JSON.stringify(layouts), JSON.stringify(blockData), "---ab");

  const isContentEditable = (key: string) => {
    return !NO_Edit_TYPE.includes(key.split("-")[0]);
  };

  const handlePre = () => {
    setPrintNodes((v) => {
      const arr = [...v];

      arr[page] = document.querySelector(
        `#${CONSTANTS.PREVIEW_BOX}${page}`
      )!.innerHTML;
      return arr;
    });
    setPage(page - 1);
  };

  const handleNext = () => {
    const node = document.querySelector(`#${CONSTANTS.PREVIEW_BOX}${page}`)!;

    setPrintNodes((v) => {
      const arr = [...v];
      arr[page] = node.innerHTML;
      return arr;
    });
    setPage(page + 1);
    if (page + 1 === total) {
      setLayouts([...layouts, []]);
      setBlockData([...blockData, {}]);
    }
  };

  return (
    <div className="App">
      <Header
        addBlock={addBlock}
        setTheme={setTheme}
        setLayouts={setLayouts}
        setBlockData={setBlockData}
        setBgNodes={setBgNodes}
        printNodes={printNodes}
        setPrintNodes={setPrintNodes}
        setTotal={setTotal}
        setPage={setPage}
        page={page}
      />
      <div className="flex justify-between bg-gray-300 py-20">
        {/* edit */}
        <div className="w-6/12 flex justify-center relative">
          <div
            className="mx-1 my-4 layout-box bg-white p-2 min-h-screen relative"
            style={{ height: 1050 }}
          >
            {bgNodes.map((item, i) => (
              <div
                style={{
                  position: "absolute",
                  backgroundColor: theme,
                  ...item,
                }}
                key={i}
              >
                <CloseOutlined
                  className="absolute top-0 right-0 text-localxs cursor-pointer z-10"
                  onClick={() => handleDeleteBgNode(i)}
                />
              </div>
            ))}
            {layouts.map((item, i) => (
              <GridLayout
                key={i}
                layout={item}
                cols={24}
                width={630}
                margin={[5, 2]}
                rowHeight={20}
                onLayoutChange={(layouts) => handleLayoutChange(layouts, i)}
                style={{ display: page === i ? "block" : "none" }}
              >
                {item.map((subItem) => (
                  <div key={subItem.i}>
                    <div
                      onBlur={(e) => handleBlockChange(subItem.i, e)}
                      contentEditable={isContentEditable(subItem.i)}
                      suppressContentEditableWarning
                      className="bg-gray-200 h-full leading-normal py-2 px-1"
                      style={{
                        ...BLOCK_STYLE[blockData[page][subItem.i]?.type || ""],
                        ...blockData[page][subItem.i]?.style,
                      }}
                    >
                      {blockData[page][subItem.i]?.text}
                    </div>
                    <CopyOutlined
                      className="absolute top-0 left-0 text-localxs cursor-pointer"
                      onClick={() => handleCopyBlock(subItem.i)}
                    />
                    <Setting
                      setBlockData={setBlockData}
                      i={subItem.i}
                      page={page}
                    />

                    <CloseOutlined
                      className="absolute top-0 right-0 text-localxs cursor-pointer"
                      onClick={() => handleDeleteBlock(subItem.i)}
                    />
                  </div>
                ))}
              </GridLayout>
            ))}
          </div>
          <Tooltip title="">
            <LeftSquareOutlined
              className="absolute text-white left-12"
              style={{
                fontSize: 60,
                top: "40%",
                display: page === 0 ? "none" : "block",
              }}
              onClick={handlePre}
            />
          </Tooltip>

          {page + 1 === layouts.length ? (
            <Tooltip title="">
              <PlusSquareOutlined
                className="absolute text-white right-12"
                style={{
                  fontSize: 60,
                  top: "40%",
                }}
                onClick={handleNext}
              />
            </Tooltip>
          ) : (
            <Tooltip title="">
              <RightSquareOutlined
                className="absolute text-white right-12"
                style={{
                  fontSize: 60,
                  top: "40%",
                }}
                onClick={handleNext}
              />
            </Tooltip>
          )}
        </div>
        {/* show */}
        <div
          id={`${CONSTANTS.PREVIEW_BOX}${page}`}
          className="w-6/12  flex justify-center"
        >
          <div
            className="mx-1 my-4 layout-box bg-white p-2 min-h-screen relative"
            style={{ height: 1050 }}
          >
            <div>
              {bgNodes.map((item) => (
                <div
                  style={{
                    position: "absolute",
                    backgroundColor: theme,
                    ...item,
                  }}
                ></div>
              ))}
              {layouts.map((item, i) => (
                <GridLayout
                  key={i}
                  layout={layouts[page]}
                  width={630}
                  cols={24}
                  margin={[5, 2]}
                  rowHeight={20}
                  isDraggable={false}
                  isResizable={false}
                  style={
                    page === i ? { display: "block" } : { display: "none" }
                  }
                >
                  {item.map((subItem) => (
                    <div key={subItem.i}>
                      <div
                        className=" h-full leading-normal py-2 px-1"
                        style={{
                          ...BLOCK_STYLE[
                            blockData[page][subItem.i]?.type || ""
                          ],
                          ...blockData[page][subItem.i]?.style,
                        }}
                      >
                        {blockData[page][subItem.i]?.text}
                      </div>
                    </div>
                  ))}
                </GridLayout>
              ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
