import React, { useState } from "react";
import { Button, Dropdown, Menu, Space } from "antd";
import { DownOutlined } from "@ant-design/icons";
// import html2pdf from "html2pdf.js";
import GridLayout from "react-grid-layout";

import DividerModal from "../divider";
import Themes from "../themes";
import Icons from "../icons";

import Logo from "../../../../assets/logo.png";
import { Data, BgNodes } from "../../index";

import { layout1, blockData1 } from "../../tpl/tpl";
import { layout2, blockData2, bgNodes2, theme2 } from "../../tpl/tpl2";
import { layout3, blockData3, theme3 } from "../../tpl/tpl3";

import { TYPE, CONSTANTS, BG_POSITION } from "../../constants";

import print from "../../../../utils/print";
interface Props {
  addBlock: ({ type, text, layout }: Data) => void;
  setTheme: React.Dispatch<React.SetStateAction<string>>;
  setLayouts: React.Dispatch<React.SetStateAction<GridLayout.Layout[][]>>;
  setBlockData: React.Dispatch<React.SetStateAction<Record<string, Data>[]>>;
  setBgNodes: React.Dispatch<React.SetStateAction<BgNodes[]>>;
  setPrintNodes: React.Dispatch<React.SetStateAction<string[]>>;
  setTotal: React.Dispatch<React.SetStateAction<number>>;
  setPage: React.Dispatch<React.SetStateAction<number>>;
  printNodes: string[];
  page: number;
}

export default function Header({
  addBlock,
  setTheme,
  setLayouts,
  setBlockData,
  setBgNodes,
  setPrintNodes,
  setTotal,
  setPage,
  printNodes,
  page,
}: Props) {
  const [modalVisible, setModalVisible] = useState(false);

  const handleChangeTpl = (e: any) => {
    let layout: GridLayout.Layout[][] = [[]],
      blockData: any = [{}],
      theme: string = "green";
    let bgNodes: React.CSSProperties[] = [];
    switch (Number(e.key)) {
      case 1:
        layout = layout1;
        blockData = blockData1;
        bgNodes = [];
        break;
      case 2:
        layout = layout2;
        blockData = blockData2;
        bgNodes = bgNodes2;
        theme = theme2;
        break;
      case 3:
        layout = layout3;
        blockData = blockData3;
        bgNodes = [];
        theme = theme3;
        break;
      case 0:
        layout = [[]];
        blockData = [{}];
        bgNodes = [];
        theme = "green";

        break;
    }

    setLayouts(layout);
    setBlockData(blockData);
    setTheme(theme);
    setBgNodes(bgNodes);
    setTotal(layout.length);
    setPage(0);
  };

  const handleAddBlock = (e: any) => {
    const type = e.key;
    if (type === TYPE.DIVIDER) {
      setModalVisible(true);

      return;
    }
    addBlock({
      type,
      layout: {
        w: 12,
        h: 2,
      },
    });
  };

  const handleAddBgBlock = (e: any) => {
    let style: React.CSSProperties = {
      width: "calc(100% - 1rem)",
      height: "11.5rem",
    };
    switch (e.key) {
      case BG_POSITION.LEFT:
        style = {
          width: "11.5rem",
          height: "calc(100% - 1rem)",
        };
        break;
      case BG_POSITION.BOTTOM:
        style = {
          ...style,
          bottom: "0.5rem",
        };
        break;
      case BG_POSITION.RIGHT:
        style = {
          width: "11.5rem",
          height: "calc(100% - 1rem)",
          right: "0.5rem",
        };
        break;
    }

    setBgNodes((v) => {
      return [...v, style];
    });
  };

  const handleUploadPicture = () => {
    var reads = new FileReader();
    const input = document.getElementById("uploadImage") as HTMLInputElement;
    const f = input.files?.[0] as File;

    reads.readAsDataURL(f);
    reads.onload = (e) => {
      addBlock({
        type: "img",
        text: (
          <img className="h-full" src={e.target?.result as string} alt="" />
        ),
        layout: {
          i: `${CONSTANTS.AVATAR}-${Date.now()}`,
          w: 2,
          h: 6,
        },
      });
    };
  };

  // const handleExport = () => {
  //   const element = document.getElementById("preview-box")!;
  //   const opt = {
  //     margin: 0,
  //     filename: ".pdf",
  //     image: { type: "jpeg", quality: 0.98 },
  //     html2canvas: {
  //       scale: 2,
  //       x: -80,
  //       onclone: (el: HTMLDivElement) => {
  //         const elTexts = el.querySelectorAll(
  //           "[data-node-type=text]"
  //         ) as NodeListOf<HTMLElement>;
  //         for (let i = 0; i < elTexts.length; i++) {
  //           const elText = elTexts[i];
  //           // const range = elText.ownerDocument.createRange();
  //           // range.setStart(elText.childNodes[0], 0);
  //           // range.setEnd(elText.childNodes[0], 1);
  //           // const fontSize = parseInt(elText.style.fontSize, 10);
  //           // const lineHeight = parseInt(elText.style.lineHeight, 10);
  //           // const oriRect = elText.getBoundingClientRect();
  //           // const rangeRect = range.getBoundingClientRect();
  //           // const oriTop = oriRect.top + (lineHeight - fontSize) / 2;
  //           // const rangeTop = rangeRect.bottom - fontSize;
  //           // elText.style.top = `${
  //           //   parseInt(elText.style.top, 10) - (rangeTop - oriTop)
  //           // }px`;
  //           elText.style.position = "absolute";
  //           elText.style.top = "0.2rem";
  //         }
  //       },
  //     },
  //     jsPDF: {
  //       unit: "pt",
  //       format: "a4",
  //       orientation: "portrait",
  //       hotfixes: ["px_scaling"],
  //     },
  //   };
  //   html2pdf().set(opt).from(element).save();
  // };

  function handleExport() {
    const arr = [...printNodes];
    arr[page] = document.querySelector(
      `#${CONSTANTS.PREVIEW_BOX}${page}`
    )!.innerHTML;

    setPrintNodes(arr);

    print(arr);
  }

  const handleIconClick = (
    e: React.MouseEvent<HTMLUListElement, MouseEvent>
  ) => {
    const icon = (e.target as HTMLUListElement).title;
    addBlock({
      type: "icon",
      text: icon,
      layout: {
        i: `${CONSTANTS.ICON}-${Date.now()}`,
        w: 1,
        h: 2,
      },
    });
  };

  const handleThemeClick = (
    e: React.MouseEvent<HTMLUListElement, MouseEvent>
  ) => {
    const color = (e.target as HTMLUListElement).title;
    setTheme(color);
  };

  const tpls = (
    <Menu
      onClick={handleChangeTpl}
      items={[
        {
          label: "1",
          key: 1,
        },
        {
          label: "2",
          key: 2,
        },
        {
          label: "3",
          key: 3,
        },
        {
          label: "",
          key: 0,
        },
      ]}
    />
  );

  const blocks = (
    <Menu
      onClick={handleAddBlock}
      items={[
        {
          label: "",
          key: TYPE.P,
        },
        {
          label: "------",
          key: TYPE.DIVIDER,
        },
        {
          label: <h1></h1>,
          key: TYPE.H1,
        },
        {
          label: <h2></h2>,
          key: TYPE.H2,
        },
        {
          label: <h3></h3>,
          key: TYPE.H3,
        },
      ]}
    />
  );

  const bgBlocks = (
    <Menu
      onClick={handleAddBgBlock}
      items={[
        {
          label: "",
          key: BG_POSITION.TOP,
        },
        {
          label: "",
          key: BG_POSITION.LEFT,
        },
        {
          label: "",
          key: BG_POSITION.RIGHT,
        },
        {
          label: "",
          key: BG_POSITION.BOTTOM,
        },
      ]}
    />
  );

  return (
    <header className="fixed w-full bg-white z-10 py-2 px-10 shadow-xl flex justify-between">
      <h3 className="w-2/12">
        <img className="h-8" src={Logo} alt="" />
      </h3>

      <div className="w-full">
        <Dropdown overlay={tpls}>
          <Button type="text">
            <Space>
              
              <DownOutlined className="leading-6" />
            </Space>
          </Button>
        </Dropdown>
        <Dropdown overlay={blocks}>
          <Button type="text">
            <Space>
              
              <DownOutlined className="leading-6" />
            </Space>
          </Button>
        </Dropdown>
        <Dropdown overlay={bgBlocks}>
          <Button type="text">
            <Space>
              
              <DownOutlined className="leading-6" />
            </Space>
          </Button>
        </Dropdown>
        <Icons handleIconClick={handleIconClick} />
        <Button type="text">
          <input
            className="hidden"
            accept="image/*"
            id="uploadImage"
            onChange={handleUploadPicture}
            type="file"
          />
          <label htmlFor="uploadImage"></label>
        </Button>
        <Themes handleThemeClick={handleThemeClick} />
      </div>
      <Button type="dashed" onClick={handleExport}>
        PDF
      </Button>
      <i
        title="icon-github"
        className="iconfont icon-github rounded-full h-10 w-10 pt-1.5 pl-3 cursor-pointer"
        onClick={() =>
          window.open("https://github.com/MasonEast/MasonEast.github.io")
        }
      />
      <DividerModal
        visible={modalVisible}
        setVisible={setModalVisible}
        addBlock={addBlock}
      />
    </header>
  );
}
