import { SettingOutlined } from "@ant-design/icons";
import { Form, Popconfirm, Radio } from "antd";
import { Data } from "../..";

interface Props {
  setBlockData: React.Dispatch<React.SetStateAction<Record<string, Data>[]>>;
  i: string;
  page: number;
}

export default function Setting({ setBlockData, i, page }: Props) {
  const [form] = Form.useForm();

  const handleOk = () => {
    setBlockData((data) => {
      const arr = [...data];
      arr[page][i] = {
        ...arr[page][i],
        style: form.getFieldsValue() || {},
      };
      return arr;
    });
  };

  const content = (
    <Form form={form}>
      <Form.Item name="color" label="">
        <Radio.Group
          options={[
            {
              label: "",
              value: "#fff",
            },
            {
              label: "",
              value: "#000",
            },
            {
              label: "",
              value: "#ccc",
            },
          ]}
        />
      </Form.Item>
      <Form.Item label="" name="fontSize">
        <Radio.Group
          options={[
            {
              label: "",
              value: "1rem",
            },
            {
              label: "",
              value: "1.1rem",
            },
            {
              label: "",
              value: "1.2rem",
            },
            {
              label: "",
              value: "1.3rem",
            },
          ]}
        />
      </Form.Item>
      <Form.Item label="" name="fontWeight">
        <Radio.Group
          options={[
            {
              label: "",
              value: "",
            },
            {
              label: "",
              value: "500",
            },
          ]}
        />
      </Form.Item>
      <Form.Item label="" name="textAlign">
        <Radio.Group
          options={[
            {
              label: "",
              value: "left",
            },
            {
              label: "",
              value: "center",
            },
            {
              label: "",
              value: "right",
            },
          ]}
        />
      </Form.Item>
    </Form>
  );
  return (
    <Popconfirm
      title={content}
      onConfirm={handleOk}
      icon={null}
      // onCancel={cancel}
      okText=""
      cancelText=""
    >
      <SettingOutlined
        className="absolute top-0 left-2 text-localxs cursor-pointer"
        // onClick={() => handleSetBlock(item.i)}
      />
    </Popconfirm>
  );
}
