import { Form, Input, Modal, Radio, Space } from 'antd';

import { CONSTANTS, DIVIDER_TYPES, TYPES_FN } from '../../constants';
import { Data } from '../../index';

interface Props {
  visible: boolean;
  setVisible: React.Dispatch<React.SetStateAction<boolean>>;
  addBlock: ({ type, text, layout }: Data) => void;
  theme?: string;
}

export default function Divider({ visible, setVisible, addBlock }: Props) {
  const [form] = Form.useForm();
  const handleOk = () => {
    form.validateFields().then((v) => {
      addBlock({
        type: v.type,
        title: v.title,
        theme: '#ccc',
        layout: {
          i: `${CONSTANTS.DIVIDER}-${Date.now()}`,
          w: 24,
          h: 2,
        },
      });
      setVisible(false);
    });
  };

  const handleCancel = () => {
    setVisible(false);
  };

  return (
    <Modal
      title=""
      visible={visible}
      onOk={handleOk}
      onCancel={handleCancel}
      okText={''}
      cancelText={''}
    >
      <Form form={form}>
        <Form.Item label="" name="title" rules={[{ required: true }]}>
          <Input />
        </Form.Item>
        <Form.Item noStyle shouldUpdate>
          {({ getFieldValue }) => {
            const v = getFieldValue('title');
            return (
              <Form.Item
                label=""
                name="type"
                initialValue={DIVIDER_TYPES.LINE_BOTTOM}
              >
                <Radio.Group>
                  <Space direction="vertical">
                    <Radio value={DIVIDER_TYPES.LINE_BOTTOM}>
                      <div style={{ width: 350 }}>
                        {TYPES_FN(DIVIDER_TYPES.LINE_BOTTOM, v)}
                      </div>
                    </Radio>
                    <Radio value={DIVIDER_TYPES.BACKGROUND}>
                      <div style={{ width: 350 }}>
                        {TYPES_FN(DIVIDER_TYPES.BACKGROUND, v)}
                      </div>
                    </Radio>
                    <Radio value={DIVIDER_TYPES.RADIUS_LINE}>
                      <div style={{ width: 350 }}>
                        {TYPES_FN(DIVIDER_TYPES.RADIUS_LINE, v)}
                      </div>
                    </Radio>
                  </Space>
                </Radio.Group>
              </Form.Item>
            );
          }}
        </Form.Item>
      </Form>
    </Modal>
  );
}
