export const CONSTANTS = {
  AVATAR: "avatar",
  DIVIDER: "divider",
  ICON: "icon",
  PREVIEW_BOX: "preview-box",
  FOOTNOTES: "footnotes",
};

export const TYPE = {
  H1: "h1",
  H2: "h2",
  H3: "h3",
  IMG: "img",
  P: "p",
  DIVIDER: "divider",
};

export const NO_Edit_TYPE = ["img"].concat(Object.values(CONSTANTS));

export const BLOCK_STYLE: Record<string, React.CSSProperties> = {
  h1: {
    fontSize: "1.3rem",
    fontWeight: "500",
    lineHeight: "1.5rem",
  },
  h2: {
    fontSize: "1.2rem",
    fontWeight: "500",
    lineHeight: "1.5rem",
  },
  h3: {
    fontSize: "1.1rem",
    fontWeight: "500",
    lineHeight: "1.5rem",
  },
  p: {
    lineHeight: "1.5rem",
  },
};

export const DIVIDER_TYPES = {
  LINE_BOTTOM: "line-bottom",
  BACKGROUND: "background",
  BACKGROUND2: "background2",
  RADIUS_LINE: "radius-line",
};

export const BG_POSITION = {
  TOP: "top",
  LEFT: "left",
  BOTTOM: "bottom",
  RIGHT: "right",
};

export const TYPES_FN = (
  type: string,
  v: string = "",
  theme: string = "green"
) => {
  const TYPES = {
    [DIVIDER_TYPES.LINE_BOTTOM]: (
      <div
        style={{
          color: theme,
          borderBottom: `2px solid ${theme}`,
          width: "100%",
          height: "2rem",
        }}
      >
        <span data-node-type="text" style={BLOCK_STYLE.h2}>
          {v}
        </span>
      </div>
    ),
    [DIVIDER_TYPES.BACKGROUND]: (
      <div
        style={{
          background: theme,
          padding: 5,
          width: "100%",
          height: "2rem",
        }}
      >
        <span
          data-node-type="text"
          style={{ ...BLOCK_STYLE.h2, color: "#fff" }}
        >
          {v || "xxxx"}
        </span>
      </div>
    ),
    [DIVIDER_TYPES.BACKGROUND2]: (
      <div
        style={{
          background: theme,
          padding: 5,
          width: "100%",
          height: "2rem",
        }}
      >
        <span
          data-node-type="text"
          style={{ ...BLOCK_STYLE.h2, color: "#fff" }}
        >
          {v || "xxxx"}
        </span>
      </div>
    ),
    [DIVIDER_TYPES.RADIUS_LINE]: (
      <div>
        <p
          style={{
            background: theme,
            padding: 5,
            width: 150,
            display: "inline-block",
            borderRadius: 15,
            textAlign: "center",
            color: "#fff",
            height: "2rem",
          }}
        >
          <span data-node-type="text" style={BLOCK_STYLE.h2}>
            {v || "xxxx"}
          </span>
        </p>
        <p
          style={{
            borderTop: `2px solid ${theme}`,
            display: "inline-block",
            verticalAlign: "middle",
            width: "calc(100% - 150px)",
          }}
        ></p>
      </div>
    ),
  };

  return TYPES[type];
};
